import React, { Component } from 'react'


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUsers, faFighterJet, faTrophy } from '@fortawesome/free-solid-svg-icons'
import { Row, Col } from 'antd';
import Player from '../../components/player'

import './battle.less';

class battle extends Component {
    render() {
        return (
          <div>
            <div className="instruction">
              <Row>
                <Col xs={{ span: 24 }} lg={{ span: 24 }}><p>Instructions</p></Col>
              </Row>
              <Row>
                <ul className="instruction-ul">
                  <Col xs={{ span: 8 }} lg={{ span: 8 }}>
                    <li>
                      <figure>
                        <figcaption>Enter two Github:</figcaption>
                        <FontAwesomeIcon icon={faUsers} size="4x" color="#ffbf74" />
                      </figure>
                    </li>
                  </Col>
                  <Col xs={{ span: 8 }} lg={{ span: 8 }}>
                    <li>
                      <figure>
                        <figcaption>Battle</figcaption>
                        <FontAwesomeIcon icon={faFighterJet} size="4x" color="#808080" />
                      </figure>
                    </li>
                  </Col>
                  <Col xs={{ span: 8 }} lg={{ span: 8 }}>
                    <li>
                      <figure>
                        <figcaption>See the winner</figcaption>
                        <FontAwesomeIcon icon={faTrophy} size="4x" color="#f4ea2a" />
                      </figure>
                    </li>
                  </Col>
                </ul>
              </Row>
            </div>
            <Player />
          </div>
        )
    }
}

export default battle
